<template>
    <div style="width: 100%">
        <div id="loading" v-if="!loaded">
            <a-spin tip="加载中" size="large"/>
        </div>
        <iframe id="index-iframe" :src="url" crolling="no" frameborder="0" style="width: 100%"></iframe>
    </div>
</template>

<script>

    export default {
        data() {
            return {
                url: this.$store.state.common.serverUrl + 'index/1',
                loaded: false
            }
        },
        methods: {

        },
        mounted() {
            var ifm = document.getElementById("index-iframe");
            ifm.height = document.documentElement.clientHeight;

            window.onresize = function () {
                var ifm = document.getElementById("index-iframe");
                ifm.height = document.documentElement.clientHeight;
            }

            let _self = this;
            document.getElementById("index-iframe").addEventListener("load", () => {
                _self.loaded = true;
            }, false);
        }
    }
</script>

<style scoped>
    #loading {
        height: 100%;
        text-align: center;
        border-radius: 4px;
        padding: 200px 50px;
        margin: 20px 0 20px;
    }
</style>